* {
  box-sizing: border-box;
}

a {
  color: #666;
  text-decoration: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.main-page {
  flex-flow: column;
  align-items: center;
  display: flex;
}

.main-page__logo {
  border-radius: 180px;
}

.main-page__list {
  flex-flow: column;
  align-items: center;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.main-page__link {
  border-radius: 5px;
  margin-top: 10px;
  padding: 5px;
}

.main-page__link:hover, .main-page__link:hover a {
  color: #000;
  background-color: #d3d3d3;
}

.form {
  justify-content: center;
  display: flex;
}

.form__input {
  width: 100px;
  height: 30px;
  border-radius: 5px;
  margin-right: 10px;
}

.form__input:hover, .form__input:focus {
  color: #000;
}

.button {
  border: none;
  border-radius: 5px;
}

.button:hover {
  color: #000;
  background-color: #d3d3d3;
}

.button__bottom__back {
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 5px;
  margin-right: 15px;
}

.button__bottom__back:hover {
  color: #000;
  background-color: #d3d3d3;
}

.button__bottom__next {
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 5px;
}

.button__bottom__next:hover {
  color: #000;
  background-color: #d3d3d3;
}

.button-container {
  flex-direction: row;
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

.card-container {
  width: 250px;
  height: 400px;
  flex-direction: column;
  align-content: center;
  align-items: center;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.character-card__container {
  width: 250px;
  height: 400px;
  border: 2px solid #d3d3d3;
  border-radius: 10px;
  flex-direction: column;
  align-content: center;
  align-items: center;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.character-card__img {
  border-bottom: 2px solid #d3d3d3;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.character-card__name, .character-card__gender {
  text-align: center;
  margin: 0;
}

.character-card__location {
  text-align: center;
  margin: 0;
  font-size: 15px;
}

.location-name {
  text-align: center;
  font-size: 30px;
}

.resident-card__container {
  width: 250px;
  height: 400px;
  border: 1px solid #666;
}

.resident-card {
  flex-wrap: wrap;
  justify-content: space-evenly;
  display: flex;
}

.resident-card__container {
  border-radius: 10px;
  margin: 10px;
}

.resident-card__img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.resident-card__name {
  text-align: center;
  margin: 0;
  font-size: 25px;
}

.resident-card__gender {
  text-align: center;
  margin: 0;
}

.resident-card__location {
  text-align: center;
  margin: 0;
  font-size: 15px;
}

